._nav {
  border-bottom: 1px solid black;
  padding: 20px;
  filter: drop-shadow(0px 4px 6px grey);
  background-color: #fff;
  text-align: center;
}

._link {
  font-size: 28px;
  margin: 0 20px;
  color: black;
  text-decoration: none;
  background-color: cornflowerblue;
  padding: 5px 20px;
  border-radius: 5px;
  filter: drop-shadow(0px 4px 6px grey);
}
._link:hover {
  filter: drop-shadow(0px 2px 3px grey);
}

._activeLink {
  color: lightblue;
}
